                ins: 0,
                result: false,
                searchContent: '',
                seachStatus: false,
                list: [],
                isNoMoreData: false,
                busy: false,
                searchs: {
                    page: 1,
                    pageSize: 15,
                    did: this.$route.query.did
                }
            };
        },
        created() {
            this.getArticleList();
    
    
        },
        mounted() {
            // 绑定键盘
            this.geThotList();
            $('form').on('submit', function(e) {
                this.search();
                return false;
            });
        },
        methods: {
            geThotList() {
                let arr = [];
                let jarr = {};
                mockData.forEach((el, index) => {
                    console.log(index)
                    if (!jarr[index] && !jarr[index + 1]) {
                        arr.push([mockData[index], mockData[index + 1]])
                        jarr[index] = 1;
                        jarr[index + 1] = 1;
                    }
    
                });
                console.log(arr);
            },
            focusBar() {
                this.seachStatus = true;
    
                this.$nextTick(e => {
                    this.$refs.box.focus();
                });
            },
            back() {
                history.back();
            },
            changTab(str) {
                this.ins = str;
            },
            goto(str) {
                window.location.href = str;
            },
            loadMore() {
                this.busy = true;
                this.getArticleList();
            },
            search() {
                this.result = true;
                this.seachStatus = false;
            },
            getArticleList() {
                console.log(this.search.page, this.list.length)
                mockData.forEach(el => {
                    this.list.push(el);
    
    
                });
                this.busy = false;
                this.search.page++;
    
                return false;
                let url = '/youyi/common/getArticle';
                let params = this.search;
                this.$http({
                    method: 'get',
                    url,
                    params
                }).then(res => {
                    let list = res.data.articleInfo || [];
                    if (list.length) {
                        list.forEach(item => {
                            this.list.push(item);
                        });
                        this.busy = false;
                        this.search.page++;
                    } else {
                        this.isNoMoreData = true;
                    }
                });
    
            }
    
        }
    };
